body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

.app {
  width: 100%;
  height: 100vh;
  background: #fff; }
  .app .logo-cnt {
    position: absolute;
    top: 24px;
    right: 48px; }
    .app .logo-cnt a {
      display: flex;
      align-items: center;
      text-decoration: none; }
    .app .logo-cnt .logo-img {
      width: 30px;
      height: 20px; }
    .app .logo-cnt span {
      margin-left: 8px;
      color: #333;
      font-weight: bold;
      font-style: italic;
      font-size: 16px; }
  .app .container {
    display: flex;
    margin: 0 auto;
    height: 100vh; }
  .app .panel-left {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f8faff; }
  .app .panel-right {
    flex: 1 1;
    display: flex;
    padding-left: 120px;
    justify-content: center;
    align-items: center; }
  .app .demo-frame {
    position: relative;
    padding: 58px 22px 18px;
    width: 375px;
    height: 756px;
    background-color: #fff;
    box-shadow: 0 4px 30px 0 rgba(4, 59, 85, 0.1);
    border-radius: 60px;
    overflow: hidden; }
    .app .demo-frame .navbar {
      position: absolute;
      padding: 0 22px;
      top: 58px;
      left: 0;
      width: 100%;
      height: 40px;
      background: #fff; }
      .app .demo-frame .navbar__title {
        color: #333;
        font-size: 19px;
        font-weight: bold;
        line-height: 40px;
        text-align: center; }
      .app .demo-frame .navbar__btn-back {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        left: 22px;
        width: 40px;
        height: 40px;
        cursor: pointer; }
        .app .demo-frame .navbar__btn-back:active {
          opacity: 0.8; }
        .app .demo-frame .navbar__btn-back .icon {
          width: 20px;
          fill: #333; }
    .app .demo-frame iframe {
      padding-top: 40px;
      width: 100%;
      height: 100%; }
    .app .demo-frame .iphone-frame {
      position: absolute;
      top: 0;
      left: 0;
      width: 375px;
      height: 758px;
      background: url(/static/media/iframe_iphonex.8ea9318b.png) no-repeat;
      background-size: contain;
      pointer-events: none; }
  .app .info {
    width: 600px; }
    .app .info .subtitle {
      padding-left: 2px;
      color: #D2D1D2;
      font-size: 20px;
      font-weight: bold;
      line-height: 1.4; }
    .app .info .title {
      color: #030303;
      font-size: 40px;
      font-weight: bold;
      line-height: 1.4; }
    .app .info .desc {
      margin-top: 40px;
      color: #B8B7B9;
      font-size: 16px;
      line-height: 1.4; }
      .app .info .desc a {
        color: #B8B7B9;
        font-weight: bold;
        text-decoration: none; }
        .app .info .desc a:hover {
          opacity: 0.8; }
  .app .color-palette {
    margin-top: 64px;
    display: flex;
    align-items: center; }
    .app .color-palette .color-list {
      display: flex; }
      .app .color-palette .color-list__item {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        cursor: pointer;
        transition: all .3s; }
        .app .color-palette .color-list__item:hover, .app .color-palette .color-list__item--active {
          -webkit-transform: scale(1.2);
                  transform: scale(1.2); }
        .app .color-palette .color-list__item + .color-list__item {
          margin-left: 16px; }
    .app .color-palette .color-picker-wrapper {
      position: relative;
      margin-left: 40px; }
      .app .color-palette .color-picker-wrapper .color-picker {
        position: relative;
        display: inline-flex;
        align-items: center;
        height: 36px;
        padding: 0 10px;
        color: #1A1919;
        font-size: 14px;
        line-height: 1;
        border: 1px solid #F2EBF2;
        border-radius: 18px;
        cursor: pointer;
        transition: all .3s; }
        .app .color-palette .color-picker-wrapper .color-picker--active, .app .color-palette .color-picker-wrapper .color-picker:hover {
          border-color: #157EFF; }
        .app .color-palette .color-picker-wrapper .color-picker .selected-color {
          width: 24px;
          height: 24px;
          border-radius: 50%; }
        .app .color-palette .color-picker-wrapper .color-picker span {
          margin-left: 12px; }
      .app .color-palette .color-picker-wrapper .popup-picker {
        position: absolute;
        left: 0;
        bottom: 50px;
        width: 220px;
        height: 220px; }
        .app .color-palette .color-picker-wrapper .popup-picker__mask {
          position: fixed;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0; }
  .app .result-cnt {
    display: flex;
    align-items: center;
    margin-top: 80px; }
    .app .result-cnt__color {
      position: relative;
      width: 100px; }
      .app .result-cnt__color-text {
        position: relative;
        color: #1A1919;
        font-size: 24px;
        font-weight: bold; }
      .app .result-cnt__color-bar {
        position: absolute;
        top: 14px;
        left: 8px;
        width: 120px;
        height: 16px;
        display: block;
        opacity: 0.3; }
    .app .result-cnt__btns {
      margin-left: 64px; }
    .app .result-cnt__btn {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      padding: 0 20px;
      height: 32px;
      color: #fff;
      font-size: 14px;
      text-align: center;
      line-height: 30px;
      border-radius: 16px;
      background: #157EFF;
      cursor: pointer;
      transition: all .3s; }
      .app .result-cnt__btn:hover {
        opacity: 0.8; }
      .app .result-cnt__btn + .result-cnt__btn {
        margin-left: 24px; }
      .app .result-cnt__btn .icon {
        margin-right: 6px;
        width: 16px;
        fill: #fff; }
      .app .result-cnt__btn--download {
        color: #157EFF;
        border: 1px solid #157EFF;
        background: transparent; }
        .app .result-cnt__btn--download .icon {
          fill: #157EFF; }

